/* tslint:disable:max-line-length */

import '@telerik/kendo-theme-default/styles/packages/dropdowns';
import {
    Component,
    forwardRef,
    ElementRef,
    Input,
    Output,
    HostBinding,
    HostListener,
    EventEmitter,
    ContentChild,
    ViewChild,
    OnDestroy,
    AfterViewChecked,
    isDevMode,
    OpaqueToken,
    Renderer
} from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/observable/interval';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/take';
import 'rxjs/add/operator/takeUntil';
import 'rxjs/add/operator/concatMap';
import 'rxjs/add/operator/merge';
import { Subscription } from 'rxjs/Subscription';
import { Keys } from './common/keys';
import { IPopupSettings } from './popup-settings';
import { isPresent, guid, isDocumentAvailable } from './util';
import { DropDownsUtil as Util } from '@telerik/kendo-dropdowns-common';
import { SelectionService, ISelectionEvent } from './selection.service';
import { NavigationService } from './navigation.service';
import { NavigationConfig, NAVIGATION_CONFIG } from './navigation-config';
import { ItemTemplateDirective } from './templates/item-template.directive';
import { ValueTemplateDirective } from './templates/value-template.directive';
import { HeaderTemplateDirective } from './templates/header-template.directive';
import { FooterTemplateDirective } from './templates/footer-template.directive';
import { NavigationAction } from './navigation-action';

/**
 * @hidden
 */
export const DROPDOWNLIST_VALUE_ACCESSOR: any = {
    multi: true,
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => DropDownListComponent)
};

const NAVIGATION_SETTINGS: NavigationConfig = {
    useLeftRightArrows: true
};

const NAVIGATION_SETTINGS_PROVIDER: { provide: OpaqueToken, useValue: NavigationConfig } = {
    provide: NAVIGATION_CONFIG,
    useValue: NAVIGATION_SETTINGS
};

/**
 * Represents the Kendo UI DropDownList component for Angular.
 *
 * @example
 * ```ts
 * @@Component({
 * selector: 'my-app',
 * template: `
 *  <kendo-dropdownlist [data]="listItems">
 *  </kendo-dropdownlist>
 * `
 * })
 * class AppComponent {
 *   public listItems: Array<string> = ["Item 1", "Item 2", "Item 3", "Item 4"];
 * }
 * ```
 */
@Component({
    providers: [DROPDOWNLIST_VALUE_ACCESSOR, SelectionService, NavigationService, NAVIGATION_SETTINGS_PROVIDER],
    selector: 'kendo-dropdownlist',
    template: `
        <span [ngClass]="setHeaderClasses()" unselectable="on">
            <span #anchor [ngClass]="{ 'k-input': true }" unselectable="on">
                <template *ngIf="valueTemplate"
                    [templateContext]="{
                        templateRef: valueTemplate.templateRef,
                        $implicit: dataItem
                    }">
                </template>
                <template [ngIf]="!valueTemplate">{{ getText() }}</template>
            </span>
            <span [ngClass]="{ 'k-select': true }" unselectable="on">
                <span [ngClass]="{
                    'k-icon': true,
                    'k-i-arrow-s': true
                }"></span>
            </span>
        </span>
        <kendo-popup *ngIf="open"
            [anchor]="anchor"
            [popupClass]="setListContainerClasses()"
            [style.width]="popupWidth"
            (anchorViewportLeave)="open=false"
            (mousedown)="onMouseDown($event)">
            <!--filterable-->
            <template [ngIf]="filterable">
                <span [ngClass]="{ 'k-list-filter': true }" (click)="$event.stopImmediatePropagation()">
                    <input #filterInput
                        [(ngModel)]="filterText" [ngClass]="{ 'k-textbox': true }"
                        (input)="handleFilter($event)"
                        (focus)="onFilterFocus()" />
                    <span [ngClass]="{ 'k-icon': true, 'k-i-search': true }" unselectable="on"></span>
                </span>
            </template>
            <!--default item-->
            <template [ngIf]="defaultItem && !itemTemplate">
                <div [ngClass]="setDefaultItemClasses()" kendoDropDownsSelectable [index]="-1">
                    {{ getDefaultItemText() }}
                </div>
            </template>
            <template [ngIf]="defaultItem && itemTemplate">
                <div [ngClass]="setDefaultItemClasses()" kendoDropDownsSelectable [index]="-1">
                    <template
                        [templateContext]="{
                            templateRef: itemTemplate.templateRef,
                            $implicit: defaultItem
                        }">
                    </template>
                </div>
            </template>
            <!--header template-->
            <template *ngIf="headerTemplate"
                [templateContext]="{
                    templateRef: headerTemplate.templateRef
                }">
            </template>
            <!--list-->
            <kendo-list
                [id]="listBoxId"
                [optionPrefix]="optionPrefix"
                [data]="data"
                [textField]="textField"
                [valueField]="valueField"
                [template]="itemTemplate"
                [height]="getHeight()"
                [show]="open"
                >
            </kendo-list>
            <!--footer template-->
            <template *ngIf="footerTemplate"
                [templateContext]="{
                    templateRef: footerTemplate.templateRef
                }">
            </template>
        </kendo-popup>
  `
})

export class DropDownListComponent implements ControlValueAccessor, OnDestroy, AfterViewChecked {
    private _data: any;
    private _value: any;
    private _open: boolean = false;
    private _previousValue: any;

    private text: any;

    /**
     * Sets or gets the `open` property of the DropDownList.
     *
     * > The `open` property determines whether the popup list of the DropDownList is visible or not.
     */
    @Input()
    set open(open: boolean) {
        if (this.disabled) {
            return;
        }

        this._open = open;

        if (open) {
            this.popupWidth = this.getWidth();
        }
    }
    get open(): boolean {
        return this._open;
    }

    /**
     * Sets the data of the DropDownList.
     *
     * > The data has to be provided in an array-like list.
     */
    @Input()
    set data(data: any) {
        this._data = data || [];
    }
    get data(): any {
        return this._data;
    }

    /**
     * Sets the value of the DropDownList. It could be either of the *primivite* (string numbers)
     * or of the *complex* (objects) type. Use the `valuePrimitive` option to define the type.
     *
     * > Selected values that are not present in the source are ignored.
     */
    @Input()
    set value(newValue: any) {
        this.verifySettings(newValue);
        this._value = newValue;
    }
    get value(): any {
        return this._value;
    }

    /**
     * Sets the data item field that represents the item text.
     *
     * If the data contains only primitive values, do not define it.
     */
    @Input() public textField: string;

    /**
     * Sets the data item field that represents the item value.
     *
     * If the data contains only primitive values, do not define it.
     */
    @Input() public valueField: string;

    /**
     * Configures the popup of the DropDownList. The available options of the `IPopupSettings` are:
     *
     * - `width: Number`&mdash;Sets the width of the popup container. By default, the width of the host element is used.
     * - `height: Number`&mdash;Sets the height of the popup container. By default, the height is 200px.
     */
    @Input() public popupSettings: IPopupSettings = { height: 200 };

    /**
     * Sets the text of the default empty item. The type of the defined value has to match the data type.
     */
    @Input() public defaultItem: any;

    /**
     * Sets the disabled state of the component.
     */
    @Input() public disabled: boolean = false;

    /**
     * Enables the [filtering]({% slug overview_ddl_kendouiforangular %}#filtering) functionality of the DropDownList.
     */
    @Input() public filterable: boolean = false;

    /**
     * Enables a case-insensitive search. Use this option when `filtration` is disabled.
     */
    @Input() public ignoreCase: boolean = true;

    /**
     * Sets the delay before an item search is performed. Use this option when `filtration` is disabled.
     */
    @Input() public delay: number = 500;

    /**
     * Specifies the type of the selected value. If set to `true`, the selected value has to be of a *primitive* value.
     *
     * For more details, refer to the section on
     * the [`valuePrimitive`]({% slug overview_ddl_kendouiforangular %}#specify-the-value-type) property.
     */
    @Input() public valuePrimitive: boolean;

    /**
     * Specifies the [`tabIndex`](https://developer.mozilla.org/en-US/docs/Web/HTML/Global_attributes/tabindex) of the component.
     */
    @Input() public tabIndex: number = 0;

    /**
     * This event is triggered when value is changed.
     *
     * For more details, refer to the section on the [`valueChange`]({% slug overview_ddl_kendouiforangular %}#on-value-change) event.
     */
    @Output() public valueChange: EventEmitter<any> = new EventEmitter();

    /**
     * This event is triggered when the user types in the filter input. You can filter the source
     * based on the passed filtration value.
     *
     * For more details, refer to the section on the [`filterChange`]({% slug overview_ddl_kendouiforangular %}#on-filter-change) event.
     */
    @Output() public filterChange: EventEmitter<any> = new EventEmitter();

    /**
     * This event is triggered when the item selection is changed.
     *
     * For more details, refer to the section on
     * the [`selectionChange`]({% slug overview_ddl_kendouiforangular %}#on-change-of-item-selection) event.
     */
    @Output() public selectionChange: EventEmitter<any> = new EventEmitter<any>();

    @ContentChild(ItemTemplateDirective) public itemTemplate: ItemTemplateDirective;
    @ContentChild(ValueTemplateDirective) public valueTemplate: ValueTemplateDirective;
    @ContentChild(HeaderTemplateDirective) public headerTemplate: HeaderTemplateDirective;
    @ContentChild(FooterTemplateDirective) public footerTemplate: FooterTemplateDirective;

    @ViewChild('filterInput') public filterInput: ElementRef;


    /**
     * @hidden
     */
    @HostListener('blur') public blur(): void {
        this.wrapperBlurred.emit();
    }

    /**
     * @hidden
     */
    @HostListener('focus') public focus(): void {
        this.isFocused = true;
    }

    /**
     * @hidden
     */
    @HostListener('keydown', ['$event']) public keydown(event: any): void {
        const hasSelected: boolean = isPresent(this.selectionService.selected[0]);
        const focused: number = isNaN(this.selectionService.focused) ? 0 : this.selectionService.focused;
        let offset: number = 0;

        if (this.disabled) {
            return;
        }

        if (!hasSelected) {
            if (event.keyCode === Keys.down) {
                offset = -1;
            } else if (event.keyCode === Keys.up) {
                offset = 1;
            }
        }

        const eventData: KeyboardEvent = <KeyboardEvent>event;
        const action: NavigationAction = this.navigationService.process({
            keyCode: eventData.keyCode,
            altKey: eventData.altKey,
            min: this.defaultItem ? -1 : 0,
            max: this.data.length - 1,
            current: focused + offset
        });

        if (action !== NavigationAction.Undefined &&
            action !== NavigationAction.Tab &&
            (action !== NavigationAction.Enter || (action === NavigationAction.Enter && this.open))) {
            eventData.preventDefault();
        }
    }

    /**
     * @hidden
     */
    @HostListener('keypress', ['$event']) public keypress(event: any): void {
        this.onKeyPress(event);
    }

    /**
     * @hidden
     */
    @HostListener('click') public click(): void {
        this.open = !this.open;
        if (!this.open) {
            this.renderer.invokeElementMethod(this.wrapper, "focus");
        }
    }

    @HostBinding('class.k-widget')
    @HostBinding('class.k-dropdown')
    @HostBinding('class.k-header')
    get widgetClasses(): boolean {
        return true;
    }

    @HostBinding('attr.role') get role(): string {
        return "listbox";
    }

    @HostBinding('tabindex') get widgetTabIndex(): number {
        return this.disabled ? undefined : this.tabIndex;
    }

    @HostBinding('attr.aria-disabled') get ariaDisabled(): boolean {
        return this.disabled ? true : undefined;
    }

    @HostBinding('attr.aria-haspopup') get ariaHasPopup(): boolean {
        return true;
    }

    @HostBinding('attr.aria-expanded') get ariaExpanded(): boolean {
        return this.open;
    }

    @HostBinding('attr.aria-owns') get ariaOwns(): string {
        return this.listBoxId;
    }

    @HostBinding('attr.aria-activedescendant') get ariaActivedescendant(): string {
        return this.optionPrefix + "-" + Util.getter(this.value, this.valueField);
    }

    protected listBoxId: string = guid();
    protected optionPrefix: string = guid();

    private word: string = "";
    private last: string = "";
    private typingTimeout: any;

    protected onTouchedCallback: Function = (_: any): void => {/* lint metadata */ };
    protected onChangeCallback: Function = (value: any): void => {/* lint metadata */ };

    private selectionService: SelectionService;

    private changeSubscribtion: Subscription;
    private selectSubscribtion: Subscription;

    private navigationService: NavigationService;
    private navigationSubscribtion: Subscription;
    private enterSubscription: Subscription;
    private openSubscribtion: Subscription;
    private closeSubscription: Subscription;

    private documentClick: Observable<{}>;

    private filterFocused: EventEmitter<any> = new EventEmitter();
    private wrapperBlurred: EventEmitter<any> = new EventEmitter();

    private componentBlurredSubscription: Subscription;

    private renderer: Renderer;
    private filterText: string = "";
    private wrapper: HTMLElement;
    private popupWidth: string;
    private isFocused: boolean = false;

    constructor(selectionService: SelectionService, navigationService: NavigationService, wrapper: ElementRef, renderer: Renderer) {
        this.selectionService = selectionService;
        this.navigationService = navigationService;

        this.wrapper = wrapper.nativeElement;
        this.renderer = renderer;
        this.data = [];

        this.subscribeEvents();
    }

    /**
     * @hidden
     */
    public onFilterFocus(): void {
        this.filterFocused.emit();
    }

    public ngAfterViewChecked(): void {
        if (this.open && this.filterInput) {
            this.filterInput.nativeElement.focus();
        }
    }

    private subscribeEvents(): void {
        if (!isDocumentAvailable()) {
            return;
        }

        this.changeSubscribtion = this.selectionService.onChange.subscribe(this.handleItemChange.bind(this));
        this.selectSubscribtion = this.selectionService.onSelect.subscribe(this.handleItemSelect.bind(this));

        this.navigationSubscribtion = this.navigationService.navigate.subscribe(this.selectionService.select.bind(this.selectionService));
        this.openSubscribtion = this.navigationService.open.subscribe(() => this.open = true);
        this.closeSubscription = this.navigationService.close.subscribe(() => {
            this.open = false;
            this.renderer.invokeElementMethod(this.wrapper, "focus");
        });
        this.enterSubscription = this.navigationService.enter
            .merge(this.navigationService.esc)
            .subscribe(this.handleEnter.bind(this));

        this.documentClick = Observable
            .fromEvent(document, 'click')
            .filter((event: any) => {
                return !(this.wrapper !== event.target && this.wrapper.contains(event.target));
            });

        this.componentBlurredSubscription = this.wrapperBlurred
            .concatMap(() => Observable.interval(10).take(1).takeUntil(this.filterFocused))
            .merge(this.navigationService.tab, this.documentClick)
            .subscribe(() => {
                this.componentBlur();
            });
    }

    private unsubscribeEvents(): void {
        if (!isDocumentAvailable()) {
            return;
        }

        this.changeSubscribtion.unsubscribe();
        this.selectSubscribtion.unsubscribe();
        this.navigationSubscribtion.unsubscribe();
        this.openSubscribtion.unsubscribe();
        this.closeSubscription.unsubscribe();
        this.enterSubscription.unsubscribe();
        this.componentBlurredSubscription.unsubscribe();
    }

    private handleItemChange(event: ISelectionEvent): void {
        const index: any = event.indices.length ? event.indices[0] : undefined;
        if (!isPresent(index)) { return; }

        this.selectionChange.emit(this.data[index]);
        this.change(this.data[index]);
    }

    private handleItemSelect(event: ISelectionEvent): void {
        const index: any = event.indices.length ? event.indices[0] : undefined;

        if (!isPresent(index)) { return; }

        const dataItem: any = this.data[index] || this.defaultItem;
        if (this.open) {
            this.value = this.valuePrimitive ? Util.getter(dataItem, this.valueField) : dataItem;
            this.text = Util.getter(dataItem, this.textField);
            this.selectionChange.emit(dataItem);
        } else {
            this.selectionChange.emit(dataItem);
            this.change(dataItem);
        }
    }

    private handleEnter(): void {
        if (this.open) {
            this.change(this.data[this.selectionService.focused]);
            this.renderer.invokeElementMethod(this.wrapper, "focus");
        }
    }

    public ngOnDestroy(): void {
        this.unsubscribeEvents();
    }

    public ngOnChanges(changes: any): void {
        if (this.valuePrimitive === undefined) {
            this.valuePrimitive = this.valueField ? false : true;
        }

        this.setState(this.value);
    }

    /**
     * @hidden
     */
    public writeValue(newValue: any): void {
        this.verifySettings(newValue);
        this.setState(newValue);
    }

    /**
     * @hidden
     */
    public registerOnChange(fn: any): void {
        this.onChangeCallback = fn;
    }

    /**
     * @hidden
     */
    public registerOnTouched(fn: any): void {
        this.onTouchedCallback = fn;
    }

    protected verifySettings(newValue: any): void {
        if (!isDevMode()) { return; }

        if (this.defaultItem && this.valueField && typeof this.defaultItem !== "object") {
            throw new Error("defaultItem and data items must be of same type. See http://www.telerik.com/kendo-angular-ui/components/dropdowns/api/DropDownListComponent/#defaultitem-any");
        }
        if (this.valuePrimitive === true && isPresent(newValue) && typeof newValue === "object") {
            throw new Error("Expected initial value of primitive type. See http://www.telerik.com/kendo-angular-ui/components/dropdowns/dropdownlist/#value-selection");
        }

        if (this.valuePrimitive === false && isPresent(newValue) && typeof newValue !== "object") {
            throw new Error("Expected initial value of type Object. See http://www.telerik.com/kendo-angular-ui/components/dropdowns/dropdownlist/#value-selection");
        }
    }

    /**
     * @hidden
     */
    public setHeaderClasses(): Object {
        return {
            'k-dropdown-wrap': true,
            'k-state-default': !this.disabled,
            'k-state-disabled': this.disabled,
            'k-state-focused': this.isFocused
        };
    }

    protected setListContainerClasses(): Object {
        return {
            'k-list-container': true,
            'k-reset': true
        };
    }

    protected setDefaultItemClasses(): Object {
        return {
            'k-list-optionlabel': true
        };
    }

    protected getDefaultItemText(): any {
        return Util.getter(this.defaultItem, this.textField);
    }

    protected getText(): any {
        return this.text;
    }

    protected getWidth(): string {
        let wrapperWidth: string = "";
        let wrapperOffsetWidth: number = 0;

        if (isDocumentAvailable()) {
            wrapperWidth = this.wrapper.style.width;
            wrapperOffsetWidth = this.wrapper.offsetWidth;
        }

        const width: any = this.popupSettings.width || wrapperWidth || wrapperOffsetWidth;

        return isNaN(width) ? width : `${width}px`;
    }

    protected getHeight(): any {
        //defaultItem, filterInput, header, footer ???
        return this.popupSettings.height;
    }

    protected componentBlur(): void {
        if (this._previousValue !== this.value) {
            this.change(this.value);
        } else {
            this.open = false;
        }
        this.onTouchedCallback();

        this.isFocused = false;
    }

    protected onMouseDown(event: any): void {
        const tagName: string = event.target.tagName.toLowerCase();
        if (tagName !== "input") {
            event.preventDefault();
        }
    }

    protected onKeyPress(event: any): void {
        if (event.which === 0 || event.keyCode === Keys.enter) {
            return;
        }

        let character: string = String.fromCharCode(event.charCode || event.keyCode);

        if (this.ignoreCase) {
            character = character.toLowerCase();
        }

        if (character === " ") {
            event.preventDefault();
        }

        this.word += character;
        this.last = character;

        this.search();
    }

    protected search(): void {
        clearTimeout(this.typingTimeout);

        if (!this.filterable) {
            this.typingTimeout = setTimeout(() => { this.word = ""; }, this.delay);
            this.selectNext();
        }
    }

    protected selectNext(): void {
        let data: Array<any> = this.data.slice();
        const dataLength: number = data.length + (this.defaultItem ? 1 : 0);
        const isInLoop: boolean = Util.sameCharsOnly(this.word, this.last);
        let startIndex: number = this.selectionService.selected[0] || 0 + (this.defaultItem ? 1 : 0);
        let text: any, index: number;

        startIndex += isInLoop ? 1 : 0;
        startIndex = Util.normalizeIndex(startIndex, dataLength);

        data = Util.shuffleData(data, startIndex, this.defaultItem);

        index = 0;
        for (; index < dataLength; index++) {
            text = Util.getter(data[index], this.textField);

            if (isInLoop && Util.matchText(text, this.last, this.ignoreCase)) {
                break;
            } else if (Util.matchText(text, this.word, this.ignoreCase)) {
                break;
            }
        }

        if (index !== dataLength) {
            if (this.defaultItem && Util.getter(data[index], this.valueField) === Util.getter(this.defaultItem, this.valueField)) {
                this.navigate(-1);
            } else {
                this.navigate(Util.normalizeIndex(startIndex + index, dataLength));
            }
        }
    }

    protected change(dataItem: any): void {
        dataItem = dataItem || this.defaultItem;

        this.open = false;

        if (this._previousValue === Util.getter(dataItem, this.valueField)) {
            return;
        }

        const primitiveValue: any = Util.getter(dataItem, this.valueField);

        this.value = this.valuePrimitive ? Util.getter(dataItem, this.valueField) : dataItem;
        this.text = Util.getter(dataItem, this.textField);
        this._previousValue = this.value;

        this.onChangeCallback(this.valuePrimitive ? primitiveValue : this.value);
        this.valueChange.emit(this.valuePrimitive ? primitiveValue : this.value);

        if (this.filterable) {
            this.filterText = "";
            this.filterChange.emit(this.filterText);
        }
    }

    protected navigate(index: number): void {
        this.selectionService.select(index);
    }

    protected setState(value: any): void {
        const newValue: any = Util.getter(value, this.valueField) || value;
        let valueCandidate: any;
        let textCandidate: any;
        const filterState: boolean = this.filterable && !!this.filterText;

        if (!this.data.length) {
            valueCandidate = value;
            textCandidate = Util.getter(value, this.textField);
        } else {
            this.verifySettings(value);

            const result: any = Util.resolveValue({
                data: this.data,
                defaultItem: this.defaultItem,
                value: newValue,
                valueField: this.valueField
            });

            if (result.dataItem) {
                this.selectionService.resetSelection(result.selected);
                if (this.filterText) {
                    this.selectionService.focused = 0;
                }

                valueCandidate = this.valuePrimitive ? Util.getter(result.dataItem, this.valueField) : result.dataItem;
                textCandidate = Util.getter(result.dataItem, this.textField);
            } else if (filterState) {
                this.selectionService.resetSelection([]);
                this.selectionService.focused = 0;

                valueCandidate = this.value;
                textCandidate = this.text;
            } else {
                this.selectionService.resetSelection(this.defaultItem ? [-1] : []);
                this.selectionService.focused = 0;

                valueCandidate = this.valuePrimitive ? Util.getter(this.defaultItem, this.valueField) : this.defaultItem;
                textCandidate = Util.getter(this.defaultItem, this.textField);
            }
        }

        this.value = valueCandidate;
        this.text = textCandidate;
        this._previousValue = this.value;
    }

    protected handleFilter(event: any): void {
        this.filterChange.emit(event.target.value);
    }
}



